import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';


class Landing extends Component {
  render() {
    return(
      <div style={{width: '100%', margin: 'auto'}}>
        <Grid className="landing-grid">
          <Cell col={12}>
            <img
              src="data:image/png;base64,"
              alt="avatar"
              className="avatar-img"
              />

            <div className="banner-text">
              <h1>Cybersecurity Enthusiast And Blockchain Developer</h1>

            <hr/>

          <p>Python | Hyperledger | Docker | Cryptography | Reverse Engineering | NodeJS | MongoDB | Git</p>

        <div className="social-links">

          {/* LinkedIn */}
          <a href="https://www.linkedin.com/in/yash-sahsani-18it112/" rel="noopener noreferrer" target="_blank">
            <i className="fa fa-linkedin" aria-hidden="true" />
          </a>

          {/* Github */}
          <a href="https://github.com/YashSahsani" rel="noopener noreferrer" target="_blank">
            <i className="fa fa-github" aria-hidden="true" />
          {/* medium */}
          </a>
          <a href="https://medium.com/@18it112" rel="noopener noreferrer" target="_blank">
            <i className="fa fa-medium" aria-hidden="true" />
          </a>
        </div>
            </div>
          </Cell>
        </Grid>
      </div>
    )
  }
}

export default Landing;
